import React, { Component } from 'react';
import Repo from '../molecules/repo';
import Profile from '../molecules/profile';
import Form from '../molecules/form';
import NotFound from './notFound';

import { Redirect } from 'react-router-dom';
class Results extends Component {

    constructor(props) {
        super(props);
        this.state = {
            results: [],
        };

        this.changeDataResult = this.changeDataResult.bind(this);

    }

    changeDataResult(event) {
        this.setState({ results: event });
    }
    render() {
        const { results } = this.state;
        const { name, login, company, avatar_url, location, public_repos, followers } = this.props.location.state.item.results;
        return (
            <div className="results">

                <div className="results__container">
                    <div className="results__profile">
                        {results != '' ?
                            <Profile
                                avatar_url={results.avatar_url}
                                name={results.name}
                                login={results.login}
                                company={results.company}
                                location={results.location}
                                public_repos={results.public_repos}
                                followers={results.followers}
                            /> :
                            <Profile
                                avatar_url={avatar_url}
                                name={name}
                                login={login}
                                company={company}
                                location={location}
                                public_repos={public_repos}
                                followers={followers}
                            />}
                    </div>
                    <div className="results__repo">

                        <Form sendDataResult={this.changeDataResult} />

                        {results.login || login ?
                            <div>

                                {
                                    results.length > 0 ?
                                        <Redirect to={{
                                            pathname: '/results',
                                            search: `?q=${results.login}`,
                                        }}
                                        /> :
                                        <Redirect to={{
                                            pathname: '/results',
                                            search: `?q=${login}`,
                                        }}
                                        />
                                }
                                {results.length > 0 ?
                                    <Repo login={results.login} /> :
                                    <Repo login={login} />
                                }
                            </div> : <NotFound />
                        }
                    </div>

                </div>


            </div>
        )

    }


}



export default Results;
